.Timeline {
    display: flex;
    height: 1000px;
    min-height: 100vh;
    margin: 0;
    padding: 0;
    position: relative;
    width: 100%;
}

.Timeline .Time,
.Timeline .Chart,
.Timeline .Elections {
    display: block;
    flex: 1 1 auto;
    margin: 0;
    padding: 0;
    position: relative;
}

.Timeline .Line {
    background: rgba(255,255,255,.05);
    flex: none;
    height: 2px;
    left: 75px;
    position: absolute;
    right: 0;
    transform: translate3d(0,-50%,0);
    width: auto;
    z-index: 2;
}

.Timeline .Time {
    background: #333;
    color: #ddd;
    flex: 0 0 75px;
    max-width: 75px;
}

.Timeline .Elections {
    background: #444;
    color: #ddd;
    flex: 0 0 150px;
    max-width: 150px;
}

.Timeline .Chart {
    background: #555;
}


.Timeline .Time .year {
    display: block;
    font-size: 12px;
    height: 18px;
    left: 0;
    line-height: 18px;
    position: absolute;
    right: 0;
    text-align: right;
    transform: translate3d(0,-50%,0);
    width: auto;
    z-index: 9;
}

.Timeline .Time .year .label {
    display: block;
    height: 18px;
    line-height: 18px;
    padding-right: 8px;
    position: relative;
    width: 100%;
}

.Timeline .Time .year:after {
    background: #fff;
    content: '';
    display: block;
    height: 2px;
    position:  absolute;
    right: 0;
    top: 50%;
    transform: translate3d(0,-50%,0);
    width: 4px;
}

.Timeline .chunk {
    display: block;
    position: absolute;
    width: 20px;
    z-index: 9;
}

.Timeline .chunk .details {
    display: block;
    height: 100%;
    position: relative;
    width: 100%;
}

.Timeline .chunk .details .bullet-top,
.Timeline .chunk .details .bullet-bottom {
    background: #000;
    border-radius: 2px;
    height: 12px;
    left: 50%;
    position: absolute;
    top: 0;
    transform: translate3d(-50%,-50%,0) rotate(45deg);
    transition: zoom .25s ease-in;
    width: 12px;
}

.Timeline .chunk .details .bullet-bottom {
    bottom: 0;
    height: 2px;
    top: auto;
    transform: translate3d(-50%,50%,0)  rotate(0deg);
    width: 8px;
}

.Timeline .chunk.ghost .details .bullet-top,
.Timeline .chunk.ghost .details .bullet-bottom,
.Timeline .chunk.ghost .details .line {
    background: silver !important;
}

.Timeline .chunk .details .label {
    background: #fff;
    border-radius: 3px;
    box-shadow: 0 0 15px 5px rgba(0,0,0,.25);
    color: #000;
    display: none;
    font-size: 12px;
    left: 0;
    padding: 5px;
    position: absolute;
    text-align: center;
    top: 50%;
    transform: translate3d(calc(-100% - 10px),-50%,0);
    width: 100px;
    z-index: 10;
}

.Timeline .chunk .logo {
    background-color: #fff;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    border: 2px solid black;
    border-radius: 50%;
    display: block;
    font-size: 12px;
    height: 30px;
    left: 50%;
    padding: 5px;
    position: absolute;
    text-align: center;
    top: 50%;
    transform: translate3d(-50%,-50%,0);
    transform-origin: 50% 50%;
    transition: transform .25s ease-in;
    width: 30px;
    z-index: 10;
}

.Timeline .chunk:hover .details .label:after {
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 6px solid #fff;
  content: '';
  height: 0; 
  right: 0;
  position: absolute;
  top: 50%;
  transform: translate3d(100%,-50%,0);
  width: 0; 
  z-index: 11;
}

.Timeline .chunk:hover .details .label {
    display: block;
}

.Timeline .chunk .details .line {
    background: #000;
    bottom: 0;
    height: auto;
    left: 50%;
    position: absolute;
    top: 0;
    transform: translate3d(-50%,0,0);
    transition: transform .25s ease-in;
    width: 3px;
}

.Timeline .chunk:hover .details .bullet-bottom,
.Timeline .chunk:hover .details .bullet-top,
.Timeline .chunk:hover .details .line {
    zoom: 1.5;
}

.Timeline .connector {
    display: block;
    height: 2px;
    opacity: .75;
    position: absolute;
    right: auto;
    z-index: 8;
}

.Timeline .connector.ghost {
    background: silver !important;
    mix-blend-mode: normal !important;
    opacity: 1 !important;
}

.Timeline .area {
    background: repeating-linear-gradient(
    45deg,
    rgba(255,255,255,0),
    rgba(255,255,255,0) 2px,
    rgba(255,255,255,.05) 2px,
    rgba(255,255,255,.05) 4px
    );
    border-left:2px solid #fff;
    bottom: auto;
    display: block;
    left: auto;
    position: absolute;
    left: 0;
    right: 0;
    top: auto;
    z-index: 1;
}

.Timeline .area:before {
    background: #fff;
    bottom: auto;
    content: '';
    display: block;
    height: 1px;
    left: 0;
    position: absolute;
    left: 0;
    top: 0;
    width: 10px;
    z-index: 2;
}

.Timeline .area:after {
    background: #fff;
    bottom: 0;
    content: '';
    display: block;
    height: 1px;
    left: 0;
    position: absolute;
    left: 0;
    top: auto;
    width: 10px;
    z-index: 1;
}

.Timeline .area h2 {
    display: block;
    font-family: sans-serif;
    font-size: 10px;
    font-weight: 400;
    left: 10px;
    line-height: 2;
    margin: 0;
    padding: 0;
    position: absolute;
    text-align: right;
    text-transform: uppercase;
    top: 50%;
    transform: rotate(270deg) translate3d(-50%,0,0);
    transform-origin: 0 0;
    white-space: nowrap;
}